import React, {useState} from 'react';
import NextTask from './NextTask';

function App() {
  let [nexttask,updatetask] = useState();
  let [currarr,updatearr] = useState([]);

  const settask = () => {
    console.log(nexttask);
    updatearr((prevstate) => [...prevstate, nexttask]);
    updatetask('');
  }

  const textchanged = (event) => updatetask(event.target.value)

  const deletetask = (id) => {
    updatearr((prevstate) => prevstate.filter((text, index) => index !== id));
  }

  return (
    <>
      <div className="main_area">
        <div className="center_area">
          <h1 className="main_heading"> To-Do App</h1>
          <div className="add_task">
            <input placeholder="Enter A Task" onChange={textchanged} value = {nexttask}/>
            <span className="add" onClick={settask}>+</span>
          </div >
          <div className="delete_task">
            <ul>
              {
                currarr.map((cvalue, i) => <NextTask text={cvalue} id={i} onSelect={deletetask} />)
              }
            </ul>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;

